/* CSS Document */

body{ font-size:12px; margin:0;}

h1,h2,h3,h4,h5,h6{ margin:0; font-weight:normal; font-size:12px;}

a{ color:#000; text-decoration:none;}

ol,ul,li,dt,dd,dl{ list-style:none; padding:0; margin:0;}

p{ margin:0;}

i,em{ font-style:normal;}

